.wrapper {
    width: 100%;
    height:100px;
    position:fixed;
    background-color: #34495e;
    margin:auto;
}
.content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 160px;
    overflow: hidden;
    font-size: 20px;
    line-height: 40px;
    color: #ecf0f1;
  }
  .content__container {
    font-weight: 600;
    overflow: hidden;
    height: 40px;
    padding: 0 40px;
  }


  .content__container__text {
    display: inline;
    float: left;
    margin: 0;
  }
  .content__container__list {
    margin-top: 0;
    width:400px;
    padding-left: 200px;
    text-align: left;
    list-style: none;
    -webkit-animation-name: change;
    -webkit-animation-duration: 10s;
    -webkit-animation-iteration-count: infinite;
    animation-name: change;
    animation-duration: 10s;
    animation-iteration-count: infinite;
  }
  .content__container__list__item {
    line-height: 40px;
    margin: 0;
  }
  
  @-webkit-keyframes opacity {
    0%, 100% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
  }
  @-webkit-keyframes change {
    0%, 12.66%, 100% {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
    16.66%, 29.32% {
      -webkit-transform: translate3d(0, -25%, 0);
              transform: translate3d(0, -25%, 0);
    }
    33.32%,45.98% {
      -webkit-transform: translate3d(0, -50%, 0);
              transform: translate3d(0, -50%, 0);
    }
    49.98%,62.64% {
      -webkit-transform: translate3d(0, -75%, 0);
              transform: translate3d(0, -75%, 0);
    }
    66.64%,79.3% {
      -webkit-transform: translate3d(0, -50%, 0);
              transform: translate3d(0, -50%, 0);
    }
    83.3%,95.96% {
      -webkit-transform: translate3d(0, -25%, 0);
              transform: translate3d(0, -25%, 0);
    }
  }
  @keyframes opacity {
    0%, 100% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
  }
  @keyframes change {
    0%, 12.66%, 100% {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
    16.66%, 29.32% {
      -webkit-transform: translate3d(0, -25%, 0);
              transform: translate3d(0, -25%, 0);
    }
    33.32%,45.98% {
      -webkit-transform: translate3d(0, -50%, 0);
              transform: translate3d(0, -50%, 0);
    }
    49.98%,62.64% {
      -webkit-transform: translate3d(0, -75%, 0);
              transform: translate3d(0, -75%, 0);
    }
    66.64%,79.3% {
      -webkit-transform: translate3d(0, -50%, 0);
              transform: translate3d(0, -50%, 0);
    }
    83.3%,95.96% {
      -webkit-transform: translate3d(0, -25%, 0);
              transform: translate3d(0, -25%, 0);
    }
  }
.job {
  padding: 0.5rem;
  margin-bottom: 1rem;
  -webkit-transition: 0.1s all ease-in;
  transition: 0.1s all ease-in;
  cursor: pointer;
  display: block;
  height: 110px;
  border: 1px solid #dee2e6;
  line-height: 1;
  color: #444;
  background: #fff;
}

.green {
  color: #035642;
}

.job:hover {
  color: #035642;
  background-color: #f6f8f6;
}

.job h4 {
  margin: 0;
}

.category {
  color: #444;
  background-color: white;

  border-radius: 3px;
  padding: 0.5em 1em;
  -webkit-transition: 0.15s ease;
  transition: 0.15s ease;
  display: inline-block;
}
.category {
  margin-right: 0.5em;
  border:none;
}
.category:focus {
  outline: none;
}
.category:hover {
  background-color: white;
  cursor: pointer;
  color: #444;
  background-color: #03564220;
}

.active {
  border-bottom: 2px solid #03564280;
  background-color: #03564220;
}

.filters {
  margin: 0.5em auto;
  width: 80%;
}

.input-range {
  margin:auto;
  width: 50% !important;
  float: right;
}

.input-range__label {
  color: #1f1f1f !important;
  font-family: inherit !important;
}

.input-range__slider {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #035642;
  border: 1px solid #035642;
}

.input-range__track--active {
  background: #035642;
}

